@media print {
  body, html {
    color: #000;
    background-color: #fff;
    font-family: Times New Roman, Times, serif;
  }

  body * {
    visibility: hidden;
  }

  .print-logo, .print-logo *, #reporteContenido, #reporteContenido * {
    visibility: visible;
  }

  .print-logo {
    text-align: center;
    margin-bottom: 20px;
    font-size: 24px;
  }

  #reporteContenido {
    width: 100%;
    position: absolute;
    top: 50px;
    left: 0;
  }

  .tabla-reporte {
    border-collapse: collapse;
    width: 100%;
    margin-top: 20px;
  }

  .tabla-reporte th, .tabla-reporte td {
    text-align: left;
    border: 1px solid #ddd;
    padding: 8px;
  }

  .tabla-reporte th {
    background-color: #f2f2f2;
  }

  #btnImprimir, .no-print {
    display: none;
  }
}
